@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  .img_container:hover .user_info {
    @apply transition-all ease-in-out duration-300 bottom-0;
  }
}
@media only screen and (min-width: 680px) {
  .grid_col_2fr {
    grid-template-columns: minmax(0, 2fr) minmax(0, 1fr);
  }
}
